.loading-wrapper {
  visibility: hidden;
  opacity: 0.7;
  -webkit-transition: 0s all ease;
  -o-transition: 0s all ease;
  transition: 0s all ease;
  background: #222222;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1; }

.loading-wrapper span {
  display: block;
  width: 38px;
  height: 38px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: 19px 0 0 -19px;
  background: url(./oval.svg) no-repeat;
  margin: 0px auto; }

.loading-wrapper.show {
  visibility: visible;
  opacity: 0.7; }



